    export default {
        data() {
			return {
                authorizationStatus: false
			}
		},
         mounted () {
            var that = this;
            uni.getSetting({
                success(res) {
                    if (!res.authSetting['scope.userInfo']){
                        that.authorizationStatus = true;
                    }else {
                        uni.getUserInfo({
                            lang: "zh_CN", 
                            success(res){
                                that.globalData.userInfo = res.rawData;
                            }
                        })
                        that.authorizationStatus = false;
                    }
                }
            })
        },
        methods: {
            getUserInfo(){
                var that = this;
                uni.getUserInfo({
                    lang: "zh_CN", 
                    success(res){
                        that.authorizationStatus = false;
                        that.globalData.userInfo = res.rawData;
                    }
                })
            },
        }
    }
